import React, { useEffect, useCallback } from 'react';
import { useDispatch } from 'react-redux';
import Wrap from 'components/wrap';
import Form from 'components/form';
import { Header2 } from 'components/typography';
import Information from './information';
import Projects from './projects';
import Carousel from './carousel';
import { getTeamMember } from 'actions/team';
import useStyles from './use-styles';

const CrewMan = ({ location }) => {
	const dispatch = useDispatch();
	const fetchTeamMember = useCallback(
		() => dispatch(getTeamMember(location)),
		[dispatch]
	);
	console.log('sdf', location.pathname.lastIndexOf('/'));

	useEffect(() => {
		fetchTeamMember();
	}, []);

	const styles = useStyles();
	return (
		<section className={styles.crewMan}>
			<Information />
			<Projects />
			<Carousel />
			<ContactForm />
		</section>
	);
};

function ContactForm() {
	const styles = useStyles();
	return (
		<div className={styles.contacts}>
			<Wrap>
				<Header2 title='Get in touch' />
				<Form />
			</Wrap>
		</div>
	);
}

export default CrewMan;
